import { compose, withProps, lifecycle } from "recompose";

import Redirect from "components/foundational/Redirect";

import { RedirectProps } from "components/foundational/Redirect/types";

export default compose<RedirectProps, {}>(
  withProps(({ location }: RedirectProps) => ({
    location,
    redirectUrl: "https://brex.com/lp/brex-quarterly-review-2",
  })),
  lifecycle({
    componentDidMount() {
      setTimeout(() => {
        window.location.replace("https://brex.com/lp/brex-quarterly-review-2");
      }, 1200);
    },
  }),
)(Redirect);
